.cta{
    position: relative;
    .right-shape{
        position: absolute;
        right: 0; bottom: 27%;
        width: 60%;
        height: 100%;
        z-index: -2;
        img{
            width: 100%;
            height: auto;
        }
    }
    &-image{
        position: relative;
        .background{
            position: absolute;
            top: -5%;
            left: -15%;
            z-index: -1;
        }
    }
    &-content{
        h2{
            margin-bottom: 27px;
        }
        p{
            line-height: 28px;
            margin-bottom: 60px;
            a{
                color: $primary-color;
            }
        }
    }
}