.banner{
    padding: 150px 0 180px;
    background-image: url('../images/backgrounds/hero-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom center;
    &-text{
        margin-top: 100px;
        h1,p{
            color: $white;
        }
        h1{
            margin-bottom: 20px;
        }
        p{
            line-height: 30px;
            width: 65%;
            margin-bottom: 60px;
        }
    }
    &-image{
        position: relative;
        .icon{
            position: absolute;
            @include size(85px);
            border-radius: 50%;
            text-align: center;
            line-height: 85px;
        }
        .love{
            background-image: $primary-gradient;
            box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
            top: 7%; left: 19%;
        }
        .hand{
            background-image: $secondary-gradient;
            top: 24%; right: -24%;
        }
        .chat{
            background-image: $secondary-gradient;
            top: 55%; left: 4%;
        }
        .message{
            background-image: $primary-gradient;
            box-shadow: 0px 13px 40px 0px rgba(238, 85, 101, 0.38);
            top: 50%; right: -7%;
        }
    }
}